import React, { useState } from "react";
import { connect } from "react-redux";
import { setRepeat } from "../../redux/events/eventActions";
import Paper from "@material-ui/core/Paper";

const Loop = ({ com_id, events, set_repeat }) => {
  const [repeat, setRepeatState] = useState(0);

  function handleChange(e) {
    let val = parseInt(e.target.value);
    setRepeatState(val);
    let curr = events.repeat;
    console.log("this is curr",curr);
    curr[com_id] = val;
    set_repeat(curr);
  }
  return (
    <Paper elevation={3}>
      <div className="text-center bg-red-600 text-black text-sm p-2 my-3">
        <div className="grid grid-cols-2 my-2">
          <div className="text-white">Loop</div>
          <input
            className="mx-2 p-1 py-0 text-center"
            type="number"
            value={repeat}
            onChange={(e) => {
              handleChange(e);
            }}
          />
        </div>
        <div
          id={com_id}
          className="text-center bg-red-600 text-white text-sm"
        >
          Loop By {repeat}
        </div>
      </div>
    </Paper>
  );
};
const mapStateToProps = (state) => {
  return {
    events: state.event,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    set_repeat: (value) => dispatch(setRepeat(value)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Loop);
